<template>
    <v-text-field
        solo
        hide-details
        label="使用 中国必应 搜索..."
        append-icon="mdi-magnify"
        v-model="searchBoxText"
        class="input-search"
        autocomplete="off"
        ref="search"
        @keyup.enter="GoSearch"
        @click:append="GoSearch"
    ></v-text-field>
</template>

<script>
export default {
    name: "HomepageSearchBox",
    
    data() {
        return {
            searchBoxText: ""
        }
    },

    methods: {
        GoSearch() {
            var url = "https://cn.bing.com/search?q=" + this.searchBoxText;
            window.location.href = encodeURI(url);
        }
    }
}
</script>